@import "reboot";

html, body{
	height: 100%;
}

.loader {
  background: rgba(255,255,255,0.9) url(../img/loader-blue.svg) no-repeat center center;
  background-size: 36px;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
  top: 0px;
  left: 0px;
  border-radius: 10px;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  display: none;
  cursor: wait;
}

ul,li{
	list-style: none;
	margin: 0;
	padding: 0;
}

.wrapper{
	width: 100%;
	min-width: 320px;
	display: flex;
	flex-direction: column;
	height: 100%;
	min-height: 100%;
}

.topline{
	display: flex;
	justify-content: space-between;
	align-items: center;
  padding-top: 30px;
	padding-bottom: 30px;
	@media screen and (max-width: 1023px){
		padding-bottom: 10px;
	}
}

header{
	flex: 0 0 auto;
	min-height: 840px;
	position: relative;
	overflow: hidden;
  background: url(../img/bg-header.jpg) no-repeat 0 0 / cover;
	.container{
		position: relative;
		z-index: 1;
	}
  @media screen and (max-width: 480px){
    min-height: 720px;
    .container{
      padding-left: 0;
      padding-right: 0;
    }
  } 
}

.logo{
	max-width: 380px;
	img{
		max-width: 100%;
		height: auto;
	}
	@media screen and (max-width: 1023px){
    max-width: 280px;
		margin-left: auto;
		margin-right: auto;
	}	
}

.menu{
	display: flex;
	align-items: center;
	&__item{
		margin-left: 10px;
	}
	.btn{
		width: 260px;	
	}
	@media screen and (max-width: 1023px){
		display: none;
	}	
}

.btn{
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 0;
	text-align: center;
	vertical-align: middle;
	outline: none;
	touch-action: manipulation;
	cursor: pointer;
	background-image: none;
	border: none;
	text-decoration: none !important;
	white-space: nowrap;
	user-select: none;
	transition: all ease-out .2s;
	
	width: 100%;
	height: 56px;
	border-radius: 100px;
	
	font-weight: 600;
	font-size: 20px;
	line-height: 38px;
	color: #FEFEFE;
	
	
	&:hover, &:focus, &:active{
		outline: none;
		text-decoration: none;
	}
	
	&--line{
		border: 2px solid #EFEFF4;
		color: #FFFFFF;
		&:hover{
			background: #EFEFF4;
			color: #1D1D1D;
		}
	}
	
	&--dft{
		background: #FFFFFF;
    color: #1D1D1D;
		&:hover{
			background: darken(#FFFFFF, 7%)
		}
	}
	
	&--black{
		background: #010101;
    color: #EFEFF4;
		&:hover{
			background: lighten(#010101, 10%)
		}	
	}
}

main{
	flex: 1 0 auto;
}

footer{
	flex: 0 0 auto;
}

.container{
	max-width: 1580px;
	margin: 0 auto;
	padding: 0 30px;
	@media screen and (max-width: 1023px){
		max-width: 600px;
		padding: 0 17px;	
	}	
}

.inp{
	width: 100%;
	height: 48px;
	border: 1px solid #1D1D1D;
	border-radius: 100px;
	
	font-size: 17px;
	color: #1D1D1D;
	padding: 0 20px;
	
	outline: none;
	
	transition: .2s ease all;
	
	&::placeholder{
		color: #D0CED9;
		font-size: 16px;
	}
	
	&:focus{
		border: 1px solid #7879f1;
	}
}

textarea.inp{
	border-radius: 15px;
	padding: 10px 15px;
	resize: none;
	height: 155px;
	line-height: 24px;
}

.title{
	font-weight: 700;
	font-size: 48px;
	line-height: 88%;
	color: #000;
	text-align: center;
	@media screen and (max-width: 1024px){
		font-size: 46px;
	}	
	@media screen and (max-width: 720px){
		font-size: 42px;
	}	
	@media screen and (max-width: 480px){
		font-size: 34px;
	}	
}

.subtitle{
  font-weight: 400;
  font-size: 28px;
  line-height: 136%;
  color: #000;
  text-align: center;
}


.hbox{
  background: #121314;
  border-radius: 20px;
  padding: 40px;
  max-width: 810px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 150px;
  h1{
    font-weight: 700;
    font-size: 48px;
    line-height: 112%;
    color: #FEFEFE;
    margin-bottom: 20px;
  }
  p{
    font-weight: 400;
    font-size: 24px;
    color: #B3B0C0;
    line-height: 150%;
  }

  .btn{
    width: 260px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
  }

  @media screen and (max-width: 480px){
    width: 100%;
    margin-top: 80px;
    padding: 40px 20px;
    border-radius: 0;
    h1{
      font-size: 42px;
      br{
        display: none;
      }
    }
    p{
      font-size: 19px;
    }
  }  
}

.sb1{
  position: relative;
  &:after{
    content: "";
    position: absolute;
    top: 0; bottom: 0;
    left: 50%; right: 0;
    background: #F8FAFB;
  }
  &-row{
    position: relative;
    z-index: 1;
    display: flex;
  }
  &-in{
    flex: 0 0 50%;
    max-width: 50%;
    &--1{
      display: flex;
      align-items: center;
    }
  }

  &-list{
    display: flex;
    flex-flow: row wrap;
    padding-top: 10px;
    padding-bottom: 60px;
  }

  &-col{
    flex: 0 0 50%;
    max-width: 50%;
    padding-top: 50px;
    padding-right: 30px;
  }

  &-item{
    &__icon{
      margin-bottom: 15px;
      img{
        max-width: 40px;
        height: auto;
      }
    }
    &__title{
      font-weight: 600;
      font-size: 20px;
      line-height: 190%;
      color: #000;
      margin-bottom: 10px;
    }
    &__txt{
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      color: #83929A;
    }
  }

  &-info{
    padding-left: 50px;
    .title{
      text-align: left;
    }
    &__stitle{
      font-weight: 400;
      font-size: 28px;
      line-height: 136%;
      color: #000000;
      margin-top: 10px;
    }

    &__txt{
      font-weight: 400;
      font-size: 22px;
      line-height: 173%;
      color: #83929A;
      margin-top: 30px;
    }

    &__btn{
      width: 260px;
      margin-top: 30px;
    }
  }

  @media screen and (max-width: 1023px){
    &:after{
      display: none;
    }
    &-row{
      flex-direction: column;
    }
    &-in{
      flex: 0 0 100%;
      max-width: 100%;
    }
    &-list{
      margin-left: -15px;
      margin-right: -15px;
    }
    &-col{
      padding-left: 15px;
      padding-right: 15px;
    }
    &-info{
      padding-left: 0;
    }
  } 

  @media screen and (max-width: 480px){
    &-col{
      flex: 0 0 100%;
      max-width: 100%;
    }
  } 

}

.sb2{
  background: url(../img/bg-s2.jpg) no-repeat center top / cover;
  padding: 135px 0;
  .subtitle{
    margin-top: 10px;
  }
  &-list{
    margin-top: 60px;
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
  }
  &-col{
    flex: 0 0 50%;
    max-width: 50%;
    padding: 0 15px;
  }

  &-box{
    background: #FFFFFF;
    border-radius: 30px;
    padding: 50px;
    &--1{
      background: #010101;
      .sb2-box{
        &__sum{
          color: #FFFFFF;
        }
        &__stitle{
          color: #FFFFFF;
        }
        &__list{
          li{
            color: #FFFFFF;
          }
        }
      }
    }
    &__sum{
      font-weight: 700;
      font-size: 56px;
      line-height: 56px;
      color: #000;
    }
    &__stitle{
      font-weight: 400;
      font-size: 32px;
      line-height: 119%;
      color: #83929A;
      margin-top: 10px;
    }
    &__list{
      padding-top: 20px;
      padding-bottom: 20px;
      li{
        list-style: disc;
        list-style-position: inside;
        font-weight: 400;
        font-size: 24px;
        line-height: 158%;
        color: #83929A;
        u{
          text-decoration: line-through;
        }
      }
    }
    &__btn{
      width: 200px;
    }
  }

  @media screen and (max-width: 1023px){
    &-list{
      flex-direction: column;
    }
    &-col{
      flex: 0 0 100%;
      max-width: 100%;
    }
    &-box{
      &--1{
        margin-top: 40px;
      }
    }
  } 

  @media screen and (max-width: 480px){
    padding-bottom: 70px;
    &-box{
      padding: 50px 30px;
    }
  } 

  @media screen and (max-width: 420px){
    &-box{
      margin-left: -17px;
      margin-right: -17px;
      border-radius: 0;
      &__sum{
        font-size: 50px;
        line-height: 50px;
      }
      &__stitle{
        font-size: 30px;
      }
      &__list{
        li{
          font-size: 21px;
        }
      }
    }
  } 

}

.sb3{
  padding: 183px 0;

  .subtitle{
    margin-top: 10px;
  }

  &-list{
    padding-top: 30px;
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
  }

  &-col{
    flex: 0 0 25%;
    max-width: 25%;
    padding: 0 15px;
    padding-top: 30px;
  }

  &-item{
    background: #F8FAFB;
    border-radius: 30px;
    padding: 50px;
    text-align: center;
    height: 100%;
    &__icon{
      img{
        max-width: 80px;
        height: auto;
      }
    }
    &__title{
      font-weight: 700;
      font-size: 24px;
      line-height: 117%;
      color: #000;
      margin-top: 30px;
    }
    &__txt{
      font-weight: 400;
      font-size: 20px;
      color: #83929A;
      margin-top: 15px;
    }
  }

  @media screen and (max-width: 1300px){
    &-item{
      padding: 50px 25px;
    }
  }  

  @media screen and (max-width: 1023px){
    padding: 80px 0;
    &-list{
      flex-flow: row wrap;
    }
    &-col{
      flex: 0 0 50%;
      max-width: 50%;
    }
  } 

  @media screen and (max-width: 560px){
    &-col{
      flex: 0 0 100%;
      max-width: 100%;
    }
  } 

}

.sb4{
  padding: 104px 0;
  background: #F8FAFB;
  .subtitle{
    margin-top: 10px;
  }
  &-list{
    margin-top: 60px;
    display: flex;
  }

  &-img{
    flex: 0 0 349px;
    img{
      max-width: 100%;
      height: auto;
      border-radius: 30px;
    }
  }

  @media screen and (max-width: 1023px){
    padding: 80px 0;
    &-img{
      display: none;
    }
  } 
}

.form{
  position: relative;
  flex: 1;
  padding-left: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  &-inp{
    margin-bottom: 10px;
    .btn{
      width: 260px;
    }
  }
  @media screen and (max-width: 1200px){
    padding-left: 50px;
  } 
  @media screen and (max-width: 1023px){
    flex: 0 0 100%;
    max-width: 100%;
    padding-left: 0;
  } 
  @media screen and (max-width: 480px){
    &-inp{
      .btn{
        width: 100%;
      }
    }
  } 
}

.footer{
	&-wrap{
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-top: 40px;
		padding-bottom: 40px;
	}
	&-logo{
		img{
			max-width: 300px;
			height: auto;
		}
	}
	&-info{
		font-size: 14px;
		line-height: 171%;
		letter-spacing: 0.2px;
		color: #ACA9BB;
		a{
			color: #010101;
			text-decoration: underline;
			&:hover{
				text-decoration: none;
			}
		}
	}
	@media screen and (max-width: 720px){
		&-wrap{
			flex-direction: column;
			text-align: center;
		}
		&-logo{
			margin-bottom: 20px;
		}
	}
}




